/**
 * 
 * @param scope
 * @returns {boolean}
 */
const startCond = (scope) => {
  return scope.row.available === 1
}

/**
 * 
 * @type {[null,null,null,null,null]}
 */
const userBtnList = [
  {
    id: 'view',
    title: '',
    isShow: true,   // 
    isPermission: true  // 
  },
  {
    id: 'edit',
    title: '',
    isShow: true,   // 
    isPermission: true,  // 
    cond: startCond
  },
  {
    id: 'startStopUsing',
    title: '',
    isShow: true,   // 
    isPermission: true,  // 
    titleCompute: (scope) => {
      return scope.row.available === 1 ? '' : ''
    }
  },
  {
    id: 'key',
    title: '',
    toolTipTitle: '',
    isShow: true,   // 
    isPermission: true,  // 
    cond: startCond
  },
  {
    id: 'auth',
    title: '',
    toolTipTitle: '',
    isShow: true,   // 
    isPermission: true  // 
  }
]

export {userBtnList}



