<template lang="pug">
  kalix-search(title="" v-bind:searchFields="searchFields")
</template>
<script type="text/ecmascript-6">
  export default {
    name: 'AdminUserSearch',
    data() {
      return {
        searchFields: [
          {label: '', prop: 'userType', type: 'dict', appName: 'admin', dictType: ''},
          {label: '', prop: 'loginName'},
          {label: '/', prop: 'name'}
        ]
      }
    },
    created() {
    },
    methods: {}
  }
</script>
<style scoped lang="stylus" type="text/stylus">
</style>



