<template lang="pug">
  kalix-dialog.user-add(bizKey="user" ref="kalixBizDialog" v-bind:formModel.sync="formModel" isView)
    div.el-form(slot="dialogFormSlot")
      el-form-item(label="" prop="userType" v-bind:label-width="labelWidth")
        kalix-dict-select(v-model="formModel.userType" appName="admin" dictType="" style="width:100%" disabled)
      el-form-item(label="" prop="icon" v-bind:label-width="labelWidth")
        kalix-upload(v-model="formModel.icon" v-bind:isImage="true" style="width:100%" readonly)
      el-form-item(label="" prop="loginName" v-bind:label-width="labelWidth")
        el-input(v-model="formModel.loginName" readonly)
      el-form-item(label="" prop="available" v-bind:label-width="labelWidth")
        template(v-if="formModel.available === 1")
          | 
        template(v-else)
          | 
      template(v-if="formModel.userType === 0 || formModel.userType === 2")
        el-form-item(label="" prop="code" v-bind:label-width="labelWidth")
          el-input(v-model="formModel.code" readonly)
        el-form-item(label="" prop="position" v-bind:label-width="labelWidth")
          el-input(v-bind:value="formModel.position" readonly)
        el-form-item(label="" prop="name" v-bind:label-width="labelWidth")
          el-input(v-model="formModel.name" readonly)
        el-form-item(label="" prop="sex" v-bind:label-width="labelWidth")
          el-radio-group(v-model="formModel.sex" disabled)
            el-radio(label="")
            el-radio(label="")
        el-form-item(label="" prop="email" v-bind:label-width="labelWidth")
          el-input(v-model="formModel.email" readonly)
        el-form-item(label="" prop="phone" v-bind:label-width="labelWidth")
          el-input(v-model="formModel.phone" readonly)
        el-form-item(label="" prop="mobile" v-bind:label-width="labelWidth")
          el-input(v-model="formModel.mobile" readonly)
      template(v-else-if="formModel.userType === 1")
        el-form-item(label="" prop="code" v-bind:label-width="labelWidth")
          el-input(v-model="formModel.code" readonly)
        el-form-item(label="" prop="name" v-bind:label-width="labelWidth")
          el-input(v-model="formModel.name" readonly)
        el-form-item(label="" prop="sex" v-bind:label-width="labelWidth")
          el-radio-group(v-model="formModel.sex" disabled)
            el-radio(label="")
            el-radio(label="")
        el-form-item(label="" prop="email" v-bind:label-width="labelWidth")
          el-input(v-model="formModel.email" readonly)
        el-form-item(label="" prop="phone" v-bind:label-width="labelWidth")
          el-input(v-model="formModel.phone" readonly)
        el-form-item(label="" prop="mobile" v-bind:label-width="labelWidth")
          el-input(v-model="formModel.mobile" readonly)
      template(v-else-if="formModel.userType === 3")
        el-form-item(label="" prop="code" v-bind:label-width="labelWidth")
          el-input(v-model="formModel.code" readonly)
        el-form-item(label="" prop="name" v-bind:label-width="labelWidth")
          el-input(v-model="formModel.name" readonly)
        el-form-item(label="" prop="email" v-bind:label-width="labelWidth")
          el-input(v-model="formModel.email" readonly)
        el-form-item(label="" prop="phone" v-bind:label-width="labelWidth")
          el-input(v-model="formModel.phone" readonly)
        el-form-item(label="" prop="mobile" v-bind:label-width="labelWidth")
          el-input(v-model="formModel.mobile" readonly)
      template(v-else)
        // 
</template>

<script type="text/ecmascript-6">
  import FormModel from './model'

  export default {
    name: 'AdminUserView',
    data() {
      return {
        formModel: Object.assign({}, FormModel),
        labelWidth: '140px'
      }
    },
    methods: {
    }
  }
</script>



