<template lang="pug">
  kalix-dialog.user-add(bizKey="user" ref="kalixBizDialog" v-bind:formModel.sync="formModel" v-bind:targetURL="targetURL")
    div.el-form(slot="dialogFormSlot")
      el-form-item(label="" prop="userType" v-bind:rules="rules.userType" v-bind:label-width="labelWidth")
        kalix-dict-select(v-model="formModel.userType" appName="admin" dictType="" v-on:input="change($event)")
      template(v-if="formModel.userType === 0 || formModel.userType === 2")
        el-form-item(label="" prop="code" v-bind:label-width="labelWidth")
          el-input(v-model="formModel.code")
        el-form-item(label="" prop="position" v-bind:label-width="labelWidth")
          kalix-dict-select(v-model="formModel.position" appName="admin" dictType="" style="width:100%")
        el-form-item(label="" prop="name" v-bind:rules="rules.name" v-bind:label-width="labelWidth")
          el-input(v-model="formModel.name")
        el-form-item(label="" prop="sex" v-bind:label-width="labelWidth")
          el-radio-group(v-model="formModel.sex")
            el-radio(label="")
            el-radio(label="")
        el-form-item(label="" prop="email" v-bind:label-width="labelWidth")
          el-input(v-model="formModel.email")
        kalix-phone-form-item(v-model="formModel.phone" v-bind:label-width="labelWidth")
        kalix-mobile-form-item(v-model="formModel.mobile" v-bind:label-width="labelWidth")
      template(v-else-if="formModel.userType === 1")
        el-form-item(label="" prop="code" v-bind:rules="rules.code_xh" v-bind:label-width="labelWidth")
          el-input(v-model="formModel.code")
        el-form-item(label="" prop="name" v-bind:rules="rules.name" v-bind:label-width="labelWidth")
          el-input(v-model="formModel.name")
        el-form-item(label="" prop="sex" v-bind:label-width="labelWidth")
          el-radio-group(v-model="formModel.sex")
            el-radio(label="")
            el-radio(label="")
        el-form-item(label="" prop="email" v-bind:rules="rules.email" v-bind:label-width="labelWidth")
          el-input(v-model="formModel.email")
        kalix-phone-form-item(v-model="formModel.phone" v-bind:label-width="labelWidth")
        kalix-mobile-form-item(v-model="formModel.mobile" required v-bind:label-width="labelWidth")
      template(v-else-if="formModel.userType === 3")
        el-form-item(label="" prop="code" v-bind:rules="rules.code_qy" v-bind:label-width="labelWidth")
          el-input(v-model="formModel.code")
        el-form-item(label="" prop="name" v-bind:rules="rules.name" v-bind:label-width="labelWidth")
          el-input(v-model="formModel.name")
        el-form-item(label="" prop="email" v-bind:rules="rules.email" v-bind:label-width="labelWidth")
          el-input(v-model="formModel.email")
        kalix-phone-form-item(v-model="formModel.phone" required v-bind:label-width="labelWidth")
        kalix-mobile-form-item(v-model="formModel.mobile" v-bind:label-width="labelWidth")
      template(v-else)
        // 
      el-form-item(label="" prop="icon" v-bind:label-width="labelWidth")
        kalix-upload(v-model="formModel.icon" v-bind:isImage="true" style="width:100%")
      el-form-item(label="" prop="loginName" v-bind:rules="rules.loginName" v-bind:label-width="labelWidth")
        el-input(v-model="formModel.loginName")
      el-form-item(label="" prop="password" v-bind:rules="rules.password" v-bind:label-width="labelWidth")
        el-input(v-model="formModel.password" type="password")
      el-form-item(label="" prop="confirmPassword" v-bind:rules="rules.confirmPassword" v-bind:label-width="labelWidth")
        el-input(v-model="formModel.confirmPassword" type="password")
      el-form-item(label="" prop="available" v-bind:label-width="labelWidth")
        el-switch(v-model="formModel.available" active-text="" inactive-text="" active-value="0" inactive-value="1")
</template>
<script type="text/ecmascript-6">
  import FormModel from './model'
  import {usersURL} from '../../config.toml'

  export default {
    name: 'AdminUserAdd',
    data() {
      let validatePassword = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''))
        } else {
          if (this.formModel.confirmPassword !== '') {
            this.$refs.kalixBizDialog.$refs.dialogForm.validateField('confirmPassword')
          }
          callback()
        }
      }
      let validateConfirmPassword = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''))
        } else if (value !== this.formModel.password) {
          callback(new Error('!'))
        } else {
          callback()
        }
      }
      return {
        formModel: Object.assign({}, FormModel),
        rules: {
          userType: [{type: 'number', required: true, message: '', trigger: 'change'}],
          code_xh: [{required: true, message: '', trigger: 'blur'}],
          code_qy: [{required: true, message: '', trigger: 'blur'}],
          name: [{required: true, message: '', trigger: 'blur'}],
          loginName: [{required: true, message: '', trigger: 'blur'}],
          password: [
            {required: true, validator: validatePassword, trigger: 'blur'}
          ],
          confirmPassword: [
            {required: true, validator: validateConfirmPassword, trigger: 'blur'}
          ],
          email: [
            {required: true, message: '', trigger: 'blur'},
            {type: 'email', message: '', trigger: 'blur,change'}
          ]
        },
        targetURL: usersURL,
        labelWidth: '140px'
      }
    },
    created() {
    },
    methods: {
      change(event) {
        this.$refs.kalixBizDialog.$refs.dialogForm.clearValidate()
      }
    }
  }
</script>



