<template lang="pug">
  kalix-dialog.dataauth-add(bizKey="dataauth" ref="kalixBizDialog" v-bind:form-model.sync="formModel" v-bind:targetURL="targetURL")
    div.el-form(slot="dialogFormSlot")
      template(v-if="isEdit")
        el-form-item(label="" prop="appId" v-bind:label-width="labelWidth")
          kalix-select(v-model="formModel.appId" v-bind:requestUrl="appURL" label="text" appName="applications" placeholder="" disabled)
        el-form-item(label="" prop="menuId" v-bind:label-width="labelWidth")
          kalix-menu-select(v-model="formModel.menuId" v-bind:requestUrl="menuURL" label="text" v-bind:appName="menuAppName" placeholder="" disabled)
      template(v-else)
        el-form-item(label="" prop="appId" v-bind:rules="rules.appId" v-bind:label-width="labelWidth")
          kalix-select(v-model="formModel.appId" v-bind:requestUrl="appURL" label="text" appName="applications" placeholder="" v-on:selectChange="selectChange")
        el-form-item(label="" prop="menuId" v-bind:rules="rules.menuId" v-bind:label-width="labelWidth")
          kalix-menu-select(v-model="formModel.menuId" v-bind:requestUrl="menuURL" label="text" v-bind:appName="menuAppName" placeholder="")
      el-form-item(label="" prop="type" v-bind:rules="rules.type" v-bind:label-width="labelWidth")
        kalix-dict-select(v-model="formModel.type" appName="admin" dictType="")
      el-form-item(label="" prop="remark" v-bind:label-width="labelWidth")
        el-input(v-model="formModel.remark" type="textarea")
</template>

<script>
  import FormModel from './model'
  import {dataauthURL, appsURL} from '../../config.toml'
  import MenuSelect from '../../../components/biz/select/MenuSelect.vue'

  export default {
    name: 'AdminDataAuthAdd',
    data() {
      return {
        formModel: Object.assign({}, FormModel),
        isEdit: false,
        rules: {
          appId: [{type: 'string', required: true, message: '', trigger: 'change'}],
          menuId: [{type: 'string', required: true, message: '', trigger: 'change'}],
          type: [{type: 'number', required: true, message: '', trigger: 'change'}]
        },
        targetURL: dataauthURL,
        labelWidth: '140px',
        appURL: appsURL,
        menuURL: '',
        menuAppName: ''
      }
    },
    mounted() {
      setTimeout(() => {
        this.isEdit = this.$refs.kalixBizDialog.$data.isEdit
        if (this.formModel.appId) {
          this.menuURL = appsURL + '/' + this.formModel.appId
          this.menuAppName = this.formModel.appId + '-menus'
        }
      }, 20)
    },
    methods: {
      selectChange(item) {
        this.formModel.menuId = ''
        if (item.id) {
          this.menuURL = appsURL + '/' + item.id
          this.menuAppName = item.id + '-menus'
        }
      }
    },
    components: {
      KalixMenuSelect: MenuSelect
    }
  }
</script>



